#timeline {
  width: 100vw;

  overflow-x: hidden;
  overflow-y: hidden;
}

#nav {
  background-color: #efefe7;
  color: #00332d;
  height: 3.25rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
}

#title {
  color: #00332d;
  font-weight: bold;
  font-size: 20px;
  position: absolute;
  padding-left: 1rem;
}

.NavButton {
  float: right;
  padding: 0.75rem;
  margin-right: 0.25rem;
  background-color: #efefe7;
  font-size: large;
  cursor: pointer;
}

.NavButton:hover {
  background-color: #dddcd1;
}
